@keyframes pulse {
  0% {
    width: 0;
    height: 0;
    opacity: 0; }
  50% {
    width: 50%;
    height: 50%;
    opacity: 1; }
  100% {
    width: 100%;
    height: 100%;
    opacity: 0; } }

:local(.loader-container) {
  text-align: left;
  display: inline-block;
  position: relative;
  width: 1em;
  height: 1em; }

:local(.loader) {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute; }

:local(.circle) {
  display: inline-block;
  border: 2px solid #727981;
  border-radius: 100%;
  animation: pulse 0.8s linear infinite; }

:local(.two) {
  animation-delay: -0.4s; }



