@keyframes pulse {
  0% {
    opacity: 0.1; }
  15% {
    opacity: 1; }
  100% {
    opacity: 0.1; } }

:local(.loader) {
  display: inline-block;
  position: relative; }

:local(.circle) {
  border-radius: 100%;
  display: inline-block;
  margin-right: 0.0625em;
  background-color: #727981;
  width: 0.2em;
  height: 0.2em;
  animation: pulse 0.8s ease-in-out infinite; }
  :local(.circle):last-child {
    margin-right: 0; }

:local(.one) {
  animation-delay: -0.3s; }

:local(.two) {
  animation-delay: -0.2s; }

:local(.three) {
  animation-delay: -0.1s; }



